md`# 🤔 How to: Embed a Notebook in a React App

Squinting at them from a certain distance, [Observable notebooks](https://beta.observablehq.com/@mbostock/introduction-to-notebooks) and [React apps](https://reactjs.org/tutorial/tutorial.html) can look like close cousins: both are functional, reactive ways of writing UIs in JavaScript.

Let’s see if we can get them to play nicely together, by embedding and controlling an Observable notebook from React.

If you haven’t already read the [introduction to downloading and embedding notebooks](https://beta.observablehq.com/@jashkenas/downloading-and-embedding-notebooks), read that first. No worries, I can wait.

Now that we’re on the same page, let’s start with a small, pretty animation — particles drifting on a random walk, connected by faint lines when they approach one another.`
